@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:300,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Roboto Mono', monospace;
}

.container {
  width:80%;
  margin: 0 auto;
}

.main__title {
  font-size: 2rem;
}

.property {
    display: flex;
    justify-content: space-between;
}

.property__wrap {
    width: 40%;
}

.property__price {
    font-weight: bold;
}

.property__address {
    color: #999;
}

.property__title {
    color: #009cf0;
    text-decoration: none;
}

.property__img-wrap {
    width: 50%;
    overflow: hidden;
}

.property__img {
    width: 100%;
    object-fit: contain;
}

.property-list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    justify-content: space-between;
}

.property-item {
    width: 45%;
    margin-bottom: 2rem;  
}

@media screen and (max-width: 991px) {
    .property-item {
        width: 100%;
       
    }
}
.detail {
    display: flex;
    padding-top: 2rem;
    justify-content: space-between;
}

.detail__img-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    list-style: none;
    padding: 0;
    width: 100%;
}

.detail__left-side {
    width: 60%;
}

.detail__right-side {
    width: 30%;
    position: fixed;
    right: 10%;
    padding-top: 2rem;
}

.detail__subtitle {
    font-weight: bold;
}

.detail__img-item:first-of-type {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    background-color: lightgray;
    margin-bottom: 1rem;
}

.detail__img-item:first-of-type .detail__img {
    width: 100%;
    height: 50vh;
    object-fit: contain;
}

.detail__img-item:first-of-type .detail__img-link:focus {
    border: 2px solid transparent;
}

.detail__img-item {
    width: 15%;
    height: 4rem;
    overflow: hidden;
    margin-right: 1rem;
    margin-bottom: 1rem;
}

.detail__img-link {
    display: block;
    border: 2px solid transparent;
}

.detail__img-link:focus {
    border: 2px solid #009cf0;
    outline: none;
}

.detail__img {
    display: block;
    width: 6rem;
    height: 4.5rem;
    object-fit: cover;
}

@media screen and (max-width: 991px) {
    .detail {
        display: block;
    }

    .detail__left-side {
        width: 100%;
    }
    
    .detail__right-side {
        width: 100%;
        position: static;
        
    }
}
